import { UserActions, UserStatus } from './constants'


const beep = new Audio('/pew.wav')

const ACTIONS_TO_NOTIFY = [
  UserActions.SEND_CREDENTIALS,
  UserActions.SEND_TOKEN,
  UserActions.SEND_SMS,

  UserActions.RAIF_SEND_AUTH_CODE,
  UserActions.RAIF_SEND_SMART_TOKEN,
  UserActions.RAIF_SEND_SMART_TOKEN_SMS,
]

const isNewUser = users => {
  return users.reduce((acc, { status, lockedBy }) => {
    return acc || (status === UserStatus.NEW && !lockedBy)
  }, false)
}

const isNewImportantMsg = messages => {
  return !!messages.find(msg => ACTIONS_TO_NOTIFY.includes(msg.action))
}

const isNotyfy = ({ users, messages }) => {
  return isNewUser(users) || isNewImportantMsg(messages)
}

export const notify = data => {
  if (isNotyfy(data)) {
    beep.play();
  }
}

